import React from "react";
import EachSpace from "../eachSpace/eachSpace";

import "./maintable.css";

const MainTable = ({ state_words, set_words, generageNewData }) => {
  const onClick = (id) => {
    const cloneStateWords = [...state_words];
    const selectedWord = { ...cloneStateWords.find((w) => w.id === id) };
    const wordIndex = cloneStateWords.findIndex((w) => w.id === id);
    selectedWord.select = !selectedWord.select;
    cloneStateWords[wordIndex] = selectedWord;
    set_words(cloneStateWords);
  };

  const eachSpace = state_words.map((w, i) => {
    const uniqueId = Math.round(Math.random() * 10 ** 5 * Math.random() * 10 ** 5);
    w.id = uniqueId;
    w.type = `a${i + 1}`;
    return <EachSpace key={w.id} id={w.id} text={w.text} select={w.select} onClick={onClick} />;
  });
  return <div className="main-table">{eachSpace}</div>;
};

export default MainTable;
