import React from "react";
import "./button.css";

const Button = (props) => {
  const classes = ["btn", "dis"];
  const { generateNewData, disable } = props;
  if (disable) {
    classes[1] = "active";
  }
  return (
    <button onClick={generateNewData} disabled={!disable} className={classes.join(" ")}>
      Bingo !! เล่นอีกรอบ
    </button>
  );
};

export default Button;
