import { getFinals } from "./getFinals";

export const inFormButton = ({ type, data }) => {
  if (type === "rows") {
    let rows = ["_", "_", "_", "_", "_"]
      .map((arr, i) => {
        arr = data.slice(i * 5, i * 5 + 5);
        return arr;
      })
      .map((e) => e.map((obj) => obj.select));

    return getFinals(rows);
  }

  if (type === "cols") {
    let cols = [[], [], [], [], []].map((arr, i) => {
      for (let el of data) {
        if (
          el.type === `a${i + 1}` ||
          el.type === `a${i + 6}` ||
          el.type === `a${i + 11}` ||
          el.type === `a${i + 16}` ||
          el.type === `a${i + 21}`
        ) {
          arr.push(el);
        }
      }
      return arr;
    });

    cols = cols.map((e) => e.map((sub) => sub.select));

    return getFinals(cols);
  }

  if (type === "diag") {
    const diagsPos1 = ["a1", "a7", "a13", "a19", "a25"];
    const diagsPos2 = ["a5", "a9", "a13", "a17", "a21"];
    let diags = [[], []].map((arr, i) => {
      for (let el of data) {
        if (diagsPos1.includes(el.type) && i === 0) {
          arr.push(el.select);
        } else if (diagsPos2.includes(el.type) && i === 1) {
          arr.push(el.select);
        }
      }
      return arr;
    });

    return getFinals(diags).splice(0, 2);
  }
};
