export const getFinals = (arr) => {
  const final = ["_", "_", "_", "_", "_"].map((r, i) => {
    let valid = true;
    if (arr[i]) {
      for (let boo of arr[i]) {
        valid = valid && boo;
      }
      r = valid;
      valid = true;
      return r;
    }
  });

  return final;
};
