import React from "react";
import "./eachSpace.css";

const EachSpace = (props) => {
  const { text, select, id, onClick } = props;
  const classes = ["main-tabel__element", "darkBlue-bg"];
  if (select) {
    classes[1] = "blue-bg";
  }
  const [head, mean] = String(text).split("-");
  return (
    <div onClick={() => onClick(id)} className={classes.join(" ")}>
      <span>{head}</span>
      <span>{mean}</span>
    </div>
  );
};

export default EachSpace;
