import React, { useState, useRef, useEffect } from "react";
import Button from "../components/button/button";
import MainTable from "../components/maintable/MainTable";
import { words } from "../data/words";
import { inFormButton } from "../utils/inFromButton";

import "./mainpage.css";

const MainPage = () => {
  const [state_words, set_words] = useState(words);
  const refCols = useRef();
  const refRows = useRef();
  const refDiag = useRef();

  useEffect(() => {
    if (state_words) {
      generateNewData(state_words);
    }
  }, []);

  const generateNewData = (words) => {
    if (words) {
      const dataArray = [...words];
      let newData = [...dataArray];
      newData.sort((a, b) => +a.id - +b.id);
      newData = newData.map((w, i) => {
        w.type = `a${i + 1}`;
        w.select = false;
        return w;
      });

      set_words(newData);
    }
  };

  if (state_words[0] && state_words[0].type) {
    const cloneState = [...state_words];
    const finalDiags = inFormButton({ type: "diag", data: cloneState });
    const finalRows = inFormButton({ type: "rows", data: cloneState });
    const finalCols = inFormButton({ type: "cols", data: cloneState });

    let validCols = false;
    let validRows = false;
    let validDiags = false;

    finalCols.forEach((e) => {
      if (e) validCols = validCols || e;
    });
    finalRows.forEach((e) => {
      if (e) validCols = validRows || e;
    });
    finalDiags.forEach((e) => {
      if (e) validDiags = validDiags || e;
    });

    refCols.current = validCols;
    refRows.current = validRows;
    refDiag.current = validDiags;
  }

  return (
    <div>
      <h1 className="game-name">บิงโก คำเมืองน่ารู้</h1>
      <MainTable
        state_words={state_words}
        set_words={set_words}
        generateNewData={generateNewData}
      />
      <Button
        generateNewData={() => generateNewData(state_words)}
        disable={refCols.current || refRows.current || refDiag.current}
      />
    </div>
  );
};

export default MainPage;
